    margin: 0;
}
header{
    display: grid;
    grid-template-columns: 300px 700px 300px;
    background-color: #04082c;
    align-items: center;
    width: auto;
    height: 110px;
    text-align: center;
    justify-content: center;
}
.imgLogo{
    width: auto;
    height: 70px;
}
